import React from 'react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { darcula } from 'react-syntax-highlighter/dist/esm/styles/prism';
import './Incl.css';

const CppRandomGeneratorPage = () => {
  const code = `#include <iostream>
#include <random> //    
#include <ctime>  //   

int main() {
    //    
    std::mt19937 gen(time(0)); // Mersenne Twister
    std::uniform_int_distribution<> dist(1, 100); //   1  100

    //    5  
    std::cout << " : ";
    for (int i = 0; i < 5; ++i) {
        std::cout << dist(gen) << " ";
    }
    std::cout << std::endl;

    return 0;
}`;

  return (
    <div className="container fade-in">
      <h1>    C++</h1>
      <div className="card">
        <SyntaxHighlighter language="cpp" style={darcula}>
          {code}
        </SyntaxHighlighter>
      </div>
      <div className="explanation">
        <h2>:</h2>
        <p>
              `random`    .    :
        </p>
        <ul>
          <li>
            <strong>std::mt19937</strong>         Mersenne Twister.
          </li>
          <li>
            <strong>std::uniform_int_distribution</strong>   ,        (    1  100).
          </li>
          <li>
            <strong>time(0)</strong>      ,      .
          </li>
        </ul>
      </div>
    </div>
  );
};

export default CppRandomGeneratorPage;